$(".inspect-card__question").click(function () {
    $(".inspect-card__info").removeClass("_active")
    $(this).parents(".inspect-card__info").addClass("_active")
})

$(".inspect-card__modal-close").click(function () {
    $(".inspect-card__info").removeClass("_active")
})

$(".qa-card").click(function () {
    $(this).toggleClass("_active")
    $(this).find(".qa-card__hidden").slideToggle()
})

fixHeader()
$(window).scroll(function() {
    fixHeader()
})

function fixHeader() {
    if ($(window).scrollTop() > 59) {
        $(".header").addClass("_fix")
    } else {
        $(".header").removeClass("_fix")
    }
}
