$("[data-modal]").click(function () {
    let modalId = $(this).attr("data-modal")
    let news = $(this).attr("data-news")
    openModal(modalId,news)
})

function openModal(modalId,news = {}){
    if(modalId == 'newsModal'){
        let newsContent = JSON.parse(news)
        console.log(newsContent.title)

        $("#newsModal .news-card__date").text(newsContent.date)
        $("#newsModal .news-card__title").text(newsContent.title)
        $("#newsModal .news-card__desc").text(newsContent.desc)
    }
    closeModal()
    $(`#${modalId}`).addClass("modal--active")
    $("body").addClass("fixed-body")
}

$(".modal__bg").click(function () {
    closeModal()
})

$(".modal__close").click(function () {
    closeModal()
})


function closeModal() {
    $('.modal').removeClass("modal--active")
    $("body").removeClass("fixed-body")
}

function showHelpModal(data) {
    let desc = ``
    console.log(data.desc)

    for(let i = 0; i<data.desc.length; i++){
        // console.log(data.desc[i])
        desc = `
            ${desc} 
            <p>${data.desc[i]}</p>
        `
    }

    $(".help-modal__title").html(data.title)
    $(".help-modal__desc").html(desc)
    $(".help-modal__img img").attr("src",`img/${data.img}.svg`)

    openModal('helpModal')
}
